.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.site-container{
  display: flex;
  flex-direction: column;
  min-height: 100vh;

}
.site-header{

}
.site-content{
  flex:1; /*kör footern längst ner på sidan*/
  padding: 20px;
}
.site-footer{
  text-align:center;
  padding : 20px 0;
  background-color:#28282B;
  color: white;
}

/*home button*/
.homebutton{
  opacity: 0;
  cursor: pointer;
}

/*_____________*/

/*1337 page*/
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}

@keyframes loading {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
/*_______________________*/

/*Portfolio-list*/
.portfolio-list {
  list-style-type: none;
  padding: 0;
  text-align: center;
}

.portfolio-list li {
  margin-bottom: 5px;
}

.portfolio-list a {
  margin-bottom: 5px;
  color: gray;
}
